<script>
import Echarts from "../../../static/js/ranking";
import AppBace from "../../../static/ajax";
import RankingData from "../../../static/js/rankingData";
import Vue from "vue";
import { Toast } from "vant";
Vue.use(Toast);
import { Popup } from "vant";
Vue.use(Popup);
export default {
  data() {
    return {
      startx: "",
      starty: "",
      showStartTimeQuery: false,
      threeStyle: "",
      weekStyle: "",
      monthStyle: "",
      station: "pH",
      stationContent: [
        "pH",
        "氨氮",
        "总氮",
        "总磷",
        "总铜",
        "总镍",
        "总氰化物",
        "总铬"
        // "污染指数"
      ],
      startTime: "",
      lastTime: "",
      openTime: false,
      token: "",
      index: 0,
      list: [],
      color: "",
      value: 0,
      search: "ph",
      array: [],
      i: 0,
      backStreet: "",
      background: "",
      type: "0",
      showBtn: false,
      // arrayBtn:["监测质量排名","水质达标天数","环比变化量排名","环比排名变化"]
      arrayBtn: [
        { name: "监测值排名", colorBtn: "background:#b4d0ff;color:#005cff" },
        { name: "水质达标天数", colorBtn: "" },
        { name: "环比变化量排名", colorBtn: "" },
        { name: "环比排名变化量", colorBtn: "" }
      ],
      openedTime: false,
      currentDate: new Date(),
      minDate: new Date("1970-07-01"),
      TimeValue: "",
      endTime: "",
      factor: "",
      factorName: "",
      showStartTime: "",
      showEndTime: "",
      currentDate1: new Date(),
      minDate1: new Date("2017-07-01"),
      maxDate1: new Date(),
      timeDates: 0,
      margin: ""
    };
  },
  methods: {
    confirmTimeque(time) {
      this.showStartTimeQuery = false;
      this.endTime = AppBace.filter(time);

      var timeDates = this.timeDates;
      this.Day(timeDates, this.endTime);
    },
    selectStartTime() {
      this.showStartTimeQuery = true;
    },
    Day(time, currentDateTime) {
      if (currentDateTime === 0) {
        var timer = AppBace.DateTime(currentDateTime);
        this.startTime = AppBace.DateTime1(time, timer);
        this.endTime = timer;
      } else {
        this.startTime = AppBace.DateTime1(time, currentDateTime);
      }
      switch (time) {
        case 3:
          this.threeStyle = "";
          this.weekStyle = " border:none;color:rgb(221, 230, 249)";
          this.monthStyle = "border:none;color:rgb(221, 230, 249)";
          break;
        case 7:
          this.threeStyle = "border:none;color:rgb(221, 230, 249)";
          this.weekStyle = "";
          this.monthStyle = "border:none;color:rgb(221, 230, 249)";
          break;
        default:
          this.threeStyle = "border:none;color:rgb(221, 230, 249)";
          this.weekStyle = "border:none;color:rgb(221, 230, 249)";
          this.monthStyle = "";
          break;
      }
      this.timeDates = time;
      this.showStartTime = AppBace.filterDatePoint(this.startTime);
      this.showEndTime = AppBace.filterDatePoint(this.endTime);
      // debugger
      this.initData(this.factor, this.factorName);
      this.initDataRanking();
    },
    confirmTime(value) {
      this.openedTime = false;
      switch (this.TimeValue) {
        case "start":
          this.startTime = AppBace.filter(value);
          break;
        default:
          this.endTime = AppBace.filter(value);
          break;
      }
    },
    closeTime() {
      this.openedTime = false;
    },

    selectBtn(index, array) {
      console.log(array);
      this.arrayBtn = RankingData.selectBtn(index, array);
      this.changeRankingData(index);
    },
    selectValuePic() {
      var _this = this;
      var pickerView = new PickerView({
        bindElem: selectYinzi, // 绑定的元素,用于区别多个组件存在时回显区别，如果单个可以随意填某个元素
        data: this.stationContent, // 说明：该参数必须符合json格式 且最里层是个数组，如上面的data变量所展示的[3,4]。
        title: "筛选项", // 顶部标题文本 默认为“标题”
        leftText: "取消", // 头部左侧按钮文本 默认为‘取消’
        rightText: "确定", // 头部右侧按钮文本 默认为“确定”
        rightFn: function(selectArr) {
          // 点击头部右侧按钮的回调函数，参数为一个数组，数组对应滚轮中每项对应的值
          console.log(selectArr[0]);
          _this.station = selectArr[0];
          var name = Echarts.switchData(selectArr[0]);
          _this.initData(name, selectArr[0]);
          // console.log(JSON.stringify(selectArr))
        }
      });
    },
    close() {
      this.openTime = false;
    },
    confirmQuery() {
      // alert(1);
      this.openTime = false;
      console.log(this.station);
      this.initData(this.search);
      this.initDataRanking();
    },
    closeBottomSheet() {
      this.openTime = false;
    },
    // selectStartTime(e) {
    //   this.openedTime = true;
    //   this.TimeValue = e;
    // },
    changeRankingData(index) {
      switch (index) {
        case 0:
          this.color = "#4ed995";
          break;
        case 1:
          this.color = "#fbc02d";
          break;
        case 2:
          this.color = "#ef6c00";
          break;
        case 3:
          this.color = "#2196f3";
          break;
      }
      Echarts.EchartsRankingList(
        this.array[index].arrData,
        this.array[index].arrName,
        this.color
      );
    },
    toBack() {
      this.$router.go(-1);
    },
    openBotttomSheet() {
      this.openTime = true;
    },
    async initData(e, f) {
      var url = "/station/water-data/gridRanking";
      var startTime = this.startTime;
      var endTime = this.endTime;
      if (e === "" || e === undefined) {
        var search = "pH";
        var name = "pH";
      } else {
        var search = e;
        var name = f;
      }
      this.factor = search;
      this.factorName = name;
      var res = await AppBace.request(
        url,
        "POST",
        {
          endTime,
          startTime,
          search
        },
        this.token
      );
      console.log(res);
      if (res.data.data.length === 0) {
        Toast("此时间段，" + this.station + "的数据为空，请重新选择");
      } else {
        Echarts.EightPic(res.data.data, name);
        // this.forData(res.data.data);
        console.log("res", res);
        this.list = res.data.data;
      }
    },
    forData(res) {
      var value = 0;
      for (let index = 0; index < res.length; index++) {
        var data = res[index].value;
        res[index].value = data.toFixed(1);
        data = Number(res[index].value);
        value += data;
      }
      this.value = value;
      Echarts.EchartsRanking(
        res[0].value,
        res[0].name,
        "#4ed995",
        res[0].value,
        value
      );
      Echarts.EchartsRanking1(
        res[1].value,
        res[1].name,
        "#fbc02d",
        res[1].value,
        value
      );
      Echarts.EchartsRanking2(
        res[2].value,
        res[2].name,
        "#ef6c00",
        res[2].value,
        value
      );
    },
    async initDataRanking() {
      var url = "/station/water-data/gridRanking/analysis";
      var endTime = this.endTime;
      var search = this.station;
      var startTime = this.startTime;
      var type = this.type;
      var res = await AppBace.request(
        url,
        "POST",
        {
          endTime,
          search,
          startTime,
          type
        },
        this.token
      );
      console.log("街道", res);
      switch (type) {
        case "0":
          this.background = "color: #0051de;background: #b4d0ff;";
          this.backStreet = "color:#000;background:white";
          break;
        default:
          this.backStreet = "color:#0051de;background:#b4d0ff;";
          this.background = "color:#000;background:#FFffff";
          break;
      }
      this.forRankingData(res.data.data);
    },
    forRankingData(res) {
      this.array = [];
      for (const key in res) {
        if (res.hasOwnProperty(key)) {
          const element = res[key];
          console.log(element);
          console.log(key);
          this.array.push({
            arrName: key,
            arrData: element,
            colorBtn: "background:white"
          });
        }
      }
      // this.selectBtn(0, this.array);
      Echarts.EchartsRankingList(
        this.array[0].arrData,
        this.array[0].arrName,
        "#4ed995"
      );
      // Echarts.EchartsRankingList("", "监测值排名", "#4ed995");
    },
    dataScoll() {
      var startx, starty;
      //获得角度

      //根据起点终点返回方向 1向上 2向下 3向左 4向右 0未滑动

      //手指接触屏幕
      window.addEventListener("touchstart", this.startFun, true);
      //手指离开屏幕
      window.addEventListener("touchend", this.endFunc, true);
    },
    getAngle(angx, angy) {
      return (Math.atan2(angy, angx) * 180) / Math.PI;
    },
    startFun(e) {
      this.startx = e.touches[0].pageX;
      this.starty = e.touches[0].pageY;
    },
    endFunc(e) {
      var endx, endy;
      endx = e.changedTouches[0].pageX;
      endy = e.changedTouches[0].pageY;
      var direction = this.getDirection(this.startx, this.starty, endx, endy);
      switch (direction) {
        case 0:
          console.log("未滑动！");
          break;
        case 1:
          // console.log("向上！");
          // this.showHeader = false;
          this.margin = "margin-top:50px";
          // this.showHeaderStyle =
          // "showHeaderStyle:opacity: 0;filter:Alpha(opacity=0); transition: opacity 2s;";
          $("#showHeader").slideUp(200);
          //  $("#showHeader").slideToggle(200);

          break;
        case 2:
          // console.log("向下！");
          // this.showHeader = true;
          //  $("#showHeader").slideToggle(200);
          $("#showHeader").slideDown(200);
          this.margin = "margin-top:145px";
          break;
        case 3:
          console.log("向左！");
          break;
        case 4:
          console.log("向右！");
          break;
        default:
      }
    },
    getDirection(startx, starty, endx, endy) {
      var angx = endx - startx;
      var angy = endy - starty;
      var result = 0;

      //如果滑动距离太短
      if (Math.abs(angx) < 2 && Math.abs(angy) < 2) {
        return result;
      }

      var angle = this.getAngle(angx, angy);
      if (angle >= -135 && angle <= -45) {
        result = 1;
      } else if (angle > 45 && angle < 135) {
        result = 2;
      } else if (
        (angle >= 135 && angle <= 180) ||
        (angle >= -180 && angle < -135)
      ) {
        result = 3;
      } else if (angle >= -45 && angle <= 45) {
        result = 4;
      }

      return result;
    }
  },
  async mounted() {
    //手指接触屏幕
    window.addEventListener("touchstart", this.startFun, true);
    //手指离开屏幕
    window.addEventListener("touchend", this.endFunc, true);
    this.token = sessionStorage.getItem("token");
    this.startTime = await AppBace.DateTime(7);
    this.endTime = await AppBace.DateTime(0);
    // this.initData();
    // this.initDataRanking();
    // var date = AppBace
    this.Day(7, this.endTime);

    // console.log(AppBace.dateTimeDev('2018-08-28','2019-11-28'));
    // debugger
  },
  destroyed() {
    window.removeEventListener("touchstart", this.startFun, true);
    window.removeEventListener("touchend", this.endFunc, true);
  }
};
</script>
