  align-items: center;
  height: 40px;
  padding-left: 10px;
  /* font-size: 14px; */
}
.selectTimeFast div {
  border: 1px white solid;
  /* border-radius: 15px; */
  padding: 2px 10px;
  font-size: 12px;
  margin-right: 10px;
}
.header {
  position: fixed;
  top: 0;
  width: 100%;
}
.leftTime {
  margin-right: 10px;
}
.rightTime {
  margin-left: 10px;
}
